@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
	box-sizing: border-box;
}

html {
	font-family: "Noto Sans JP", sans-serif;
}

body {
	margin: 0;
	background-color: #ffffff;
}

.Convertor_card__10QCC {
	padding: 10px 20px;
	background-color: rgb(50, 54, 54);
	width: 400px;
	height: 300px;
	text-align: left;
}
.Convertor_card__10QCC label {
	color: cornsilk;
}
.Convertor_card__10QCC h2 {
	color: papayawhip;
}
.Convertor_from__14TAv label {
	padding-right: 20px;
}
.Convertor_to__2oR8s label {
	padding-right: 45px;
}
.Convertor_result__2ssQL {
	padding: 10px;
	color: green;
	font-size: 30px;
	box-shadow: orange;
}
.Convertor_error__3sp-L {
	padding: 10px;
	color: orange;
	font-size: 20px;
}

.Convertor_card__10QCC input[type="number"] {
	box-sizing: border-box;
	border: 2px solid #ccc;
	border-radius: 4px;
	font-size: 12px;
	background-color: white;
	padding: 12px 20px 12px 40px;
	width: 100px;
}
.Convertor_card__10QCC select {
	margin: 2px;
	padding: 12px 20px;
	border: none;
	border-radius: 4px;
	background-color: #f1f1f1;
}

